import { useState, Fragment } from "react";
import { Menu, X } from "lucide-react";
import { Dialog, Transition } from "@headlessui/react";

/*
  Responsive Sidebar component – white theme + global toggle
  - Toggle button visible at all sizes; sidebar collapses on mobile & overlays content
  - Sidebar background white, text blue-900; subtle border & shadow
  - Higher z-index to stay above content (mobile z-50, desktop z-40)
*/
export default function Sidebar() {
  const [open, setOpen] = useState(false);
  const toggle = () => setOpen(!open);

  return (
    <>
      {/* ─────────── Global Toggle Button ─────────── */}
      <button
        onClick={toggle}
        className="inline-flex items-center p-2 mt-2 ms-3 text-sm rounded-lg text-blue-600 hover:bg-blue-100 focus:outline-none focus:ring-2 focus:ring-blue-300"
      >
        <Menu className="w-6 h-6" />
        <span className="sr-only">Open sidebar</span>
      </button>

      {/* ─────────── Mobile Sidebar with Dialog overlay ─────────── */}
      <Transition show={open} as={Fragment}>
        <Dialog as="div" className="relative z-50 sm:hidden" onClose={toggle}>
          <Transition.Child
            as={Fragment}
            enter="transition-opacity ease-linear duration-200"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="transition-opacity ease-linear duration-200"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-black/40" aria-hidden="true" />
          </Transition.Child>

          <Transition.Child
            as={Fragment}
            enter="transition ease-in-out duration-300 transform"
            enterFrom="-translate-x-full"
            enterTo="translate-x-0"
            leave="transition ease-in-out duration-300 transform"
            leaveFrom="translate-x-0"
            leaveTo="-translate-x-full"
          >
            <Dialog.Panel className="fixed inset-y-0 left-0 flex w-64 max-w-full bg-white text-blue-900 border-r shadow-lg">
              <SidebarContent onClose={toggle} />
            </Dialog.Panel>
          </Transition.Child>
        </Dialog>
      </Transition>

      {/* ─────────── Static Sidebar (≥ sm) ─────────── */}
      <aside className="hidden sm:flex sm:flex-col sm:fixed sm:inset-y-0 sm:left-0 sm:z-40 sm:w-64 lg:w-72 bg-white text-blue-900 border-r shadow-sm">
        <SidebarContent />
      </aside>
    </>
  );
}

/* ------------------- Sidebar Inner Layout ------------------- */
function SidebarContent({ onClose }: { onClose?: () => void }) {
  return (
    <div className="h-full w-full flex flex-col px-4 py-6">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <h2 className="text-2xl font-semibold">Admin</h2>
        {onClose && (
          <button
            onClick={onClose}
            className="text-blue-900 hover:text-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-300 sm:hidden"
          >
            <X className="w-6 h-6" />
            <span className="sr-only">Close sidebar</span>
          </button>
        )}
      </div>

      {/* Navigation */}
      <nav className="flex-1 space-y-1 font-medium">
        <NavItem label="Dashboard" />
        <NavItem label="Kanban" />
        <NavItem label="Inbox" badge="3" />
        <NavItem label="Users" />
        <NavItem label="Products" />
      </nav>

      {/* Footer */}
      <NavItem label="Logout" />
    </div>
  );
}

/* ---------------------- Nav Item ---------------------- */
function NavItem({ label, badge }: { label: string; badge?: string }) {
  return (
    <a
      href="#"
      className="group flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-blue-50 transition-colors"
    >
      {/* Simple bullet indicator */}
      <span className="h-2 w-2 rounded-full bg-blue-300 group-hover:bg-blue-600" />
      <span className="flex-1 whitespace-nowrap">{label}</span>
      {badge && (
        <span className="ml-auto inline-flex items-center justify-center px-2 text-xs font-medium bg-blue-100 text-blue-800 rounded-full">
          {badge}
        </span>
      )}
    </a>
  );
}
